.social {
  padding: 9px 76px;

  .social__list {
    display: flex;
    justify-content: center;

    margin: 0;
    padding: 0;

    list-style-type: none;
  }

  .social__item {
    margin-right: 5px;
  }

  .social__link {
    display: block;
    padding: 10px;
  }

  .social__link span {
    @include visually-hidden;
  }
}

